@import url(https://fonts.googleapis.com/css2?family=Noto+Sans+KR:wght@100;300;400;500;700;900&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

html, body, div, span, applet, object, iframe,
h1, h2, h3, h4, h5, h6, p, blockquote, pre,
a, abbr, acronym, address, big, cite, code,
del, dfn, em, img, ins, kbd, q, s, samp,
small, strike, strong, sub, sup, tt, var,
b, u, i, center,
dl, dt, dd, ol, ul, li,
fieldset, form, label, legend,
table, caption, tbody, tfoot, thead, tr, th, td,
article, aside, canvas, details, embed, 
figure, figcaption, footer, header, hgroup, 
menu, nav, output, ruby, section, summary,
time, mark, audio, video {
	margin: 0;
	padding: 0;
	border: 0;
	font-size: 100%;
	font: inherit;
	vertical-align: baseline;
}
/* HTML5 display-role reset for older browsers */
article, aside, details, figcaption, figure, 
footer, header, hgroup, menu, nav, section {
	display: block;
}
body {
	line-height: 1.6;
    font-family: 'Noto Sans KR', sans-serif;
    font-size: 16px;
    color:#333;
}
ol, ul, li {
	list-style: none;
}
blockquote, q {
	quotes: none;
}
blockquote:before, blockquote:after,
q:before, q:after {
	content: '';
	content: none;
}
table {
	border-collapse: collapse;
	border-spacing: 0;
}


.wrap { width: 550px; margin: 50px auto; border: 1px solid #c5def4; padding: 30px; box-shadow: 5px 5px 10px #c5def4; }
.wrap h1 { font-size: 35px; text-align: center; font-weight: 600; margin-bottom: 50px; padding-top: 20px; }
.wrap ul {  }
.wrap ul li { line-height: 60px; border-bottom: 1px dashed #abb2c4; overflow: hidden; }
.wrap ul li span { cursor: pointer; color:#004a99; }
.wrap ul li.on span { color:#e6001f; text-decoration: line-through; }
.wrap ul li em { margin-left: 50px; cursor: pointer; font-size: 18px; }
.wrap ul li.on em { color:#e6001f; text-decoration: line-through; }
.wrap ul li button { float: right; width: 120px; height: 35px; margin-top: 12px; border: 2px solid #fe93a2; background: #fff; cursor: pointer; font-size: 16px; box-sizing: border-box; color:#df6c7b; border-radius: 3px; }
.wrap ul li button:hover { background: #fe93a2; color:#fff; }

.wrap .form { text-align: center; padding: 30px 0; }
.wrap .form input { width: 420px; height: 45px; padding: 0 20px; vertical-align: top; border: none; box-sizing: border-box; margin-right: 5px; outline: transparent; font-size: 16px; background: #f3f4f8; }
.wrap .form button { width: 120px; height: 45px; border: none; background: #004a99; color:#fff; cursor: pointer; font-size: 16px; }
